import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    darkMode: false,
    countries: [],
    country: []
  },
  mutations: {
    changeColor: (state, darkMode) => (state.darkMode = darkMode),
    countryData: (state, countries) => (state.countries = countries),
    country: (state, country) => (state.country = country)
  },
  actions: {
    toggleColor({ commit }) {
      this.darkMode = !this.darkMode;
      commit("changeColor", this.darkMode);
    },
    async fetchCountryData({ commit }) {
      const response = await axios.get(`https://restcountries.eu/rest/v2/all?fields=flag;name;population;region;capital;alpha3Code;`);
      commit('countryData', response.data);
    },
    async fetchRegions({ commit }, region) {
      const response = await axios.get(`https://restcountries.eu/rest/v2/region/${region}?fields=flag;name;population;region;capital;`);
      commit('countryData', response.data);
    },
    async searchCountry({ commit }, input) {
      const response = await axios.get(`https://restcountries.eu/rest/v2/name/${input}`);
      commit('countryData', response.data);
    },
    async searchFullCountry({ commit }, input) {
      const response = await axios.get(`https://restcountries.eu/rest/v2/name/${input}?fullText=true`);
      commit ('country', response.data);
    }
  },
  /** Now in order for us to get the state into our app component to display, we need to add a getter */
  getters: {
    allCountries: (state) => state.countries,
  },
  modules: {
  }
});
