<template>
  <Header :class="{ header__dark: darkMode }" class="header">
    <h1 class="header__heading">Where in the world?</h1>
    <p @click="toggleColor" class="header__toggler">
      <span class="header__icon">
        <i :class="bulb"></i>
      </span>
      {{ toggleText }}
    </p>
  </Header>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  name: "App",

  computed: {
    ...mapState(["darkMode"]),
    bulb() {
      return this.$store.state.darkMode ? "far fa-lightbulb" : "far fa-moon";
    },
    toggleText() {
      return this.$store.state.darkMode ? "Light Mode" : "Dark Mode";
    },
  },
  methods: {
    ...mapActions(["toggleColor"]),
  },
  watch: {
    darkMode: {
      handler() {
        localStorage.setItem(
          "darkMode",
          JSON.stringify(this.$store.state.darkMode)
        );
      },
    },
  },
  mounted() {
    this.$store.state.darkMode = JSON.parse(localStorage.getItem("darkMode"));
  },
};
</script>

<style src="./sass/dist/_base.css" />
